const Login = () => import(/* webpackChunkName:"login_main_welcome" */ '../views/Login.vue')
const Main = () => import(/* webpackChunkName:"login_main_welcome" */ '../views/Main.vue')
const Welcome = () => import(/* webpackChunkName:"login_main_welcome" */ '../views/Welcome.vue')
// my文件夹
const MyClient = () => import(/* webpackChunkName:"myClient_createClient_customerPublicArea" */ '../views/my/MyClient.vue')
const CreateClient = () => import(/* webpackChunkName:"myClient_createClient_customerPublicArea" */ '../views/my/CreateClient.vue')
const CustomerPublicArea = () => import(/* webpackChunkName:"myClient_createClient_customerPublicArea" */ '../views/my/CustomerPublicArea.vue')
// work文件夹
const Daily = () => import(/* webpackChunkName:"daily_checkedDaily" */ '../views/work/Daily.vue')
const CheckedDaily = () => import(/* webpackChunkName:"daily_checkedDaily" */ '../views/work/CheckedDaily.vue')
// detail文件夹
const CustomerDetail = () => import(/* webpackChunkName:"customerDetail_updateCustomerDetail" */ '../views/detail/CustomerDetail.vue')
const UpdateCustomerDetail = () => import(/* webpackChunkName:"customerDetail_updateCustomerDetail" */ '../views/detail/UpdateCustomerDetail.vue')

const EditDetail = () => import(/* webpackChunkName:"editDetail_custoerPublicAreaDetail" */ '../views/detail/EditDetail.vue')
const CustoerPublicAreaDetail = () => import(/* webpackChunkName:"editDetail_custoerPublicAreaDetail" */ '../views/detail/CustoerPublicAreaDetail.vue')

// updata文件夹
const CustomerUpdate = () => import(/* webpackChunkName:"customerUpdate_createUpdate_updateDetail" */ '../views/update/CustomerUpdate.vue')
const CreateUpdate = () => import(/* webpackChunkName:"customerUpdate_createUpdate_updateDetail" */ '../views/update/CreateUpdate.vue')
const UpdateDetail = () => import(/* webpackChunkName:"customerUpdate_createUpdate_updateDetail" */ '../views/update/UpdateDetail.vue')

// linkman文件夹 // user文件夹
const LinkmanMain = () => import(/* webpackChunkName:"linkmanMain_userControl" */ '../views/linkman/LinkmanMain.vue')
const UserControl = () => import(/* webpackChunkName:"linkmanMain_userControl" */ '../views/user/UserControl.vue')
Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/login' },
  { path: '/login', component: Login },
  {
    path: '/main',
    component: Main,
    redirect: '/welcome',
    children: [
      { path: '/welcome', component: Welcome },
      // my文件夹路由
      { path: '/my/myclient', component: MyClient },
      { path: '/my/createclient:id', component: CreateClient },
      { path: '/my/CustomerPublicArea', component: CustomerPublicArea },
      // work文件夹路由
      { path: '/work/daily', component: Daily },
      { path: '/work/checkedDaily', component: CheckedDaily },
      // detail文件夹路由
      { path: '/detail/customerDetail:id', component: CustomerDetail },
      { path: '/detail/updateCustomerDetail', component: UpdateCustomerDetail },
      { path: '/detail/editDetail:id', component: EditDetail },
      { path: '/detail/custoerPublicAreaDetail', component: CustoerPublicAreaDetail },
      // updata文件夹路由
      { path: '/update/customerUpdate', component: CustomerUpdate },
      { path: '/update/createUpdate', component: CreateUpdate },
      { path: '/update/updateDetail', component: UpdateDetail },
      // linkman文件夹路由
      { path: '/linkman/linkmanMain:id', component: LinkmanMain },
      // user文件夹路由
      { path: '/user/userControl', component: UserControl }
    ]
  },
  { path: '*', component: Errorinfo }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

// 挂载路由导航守卫
router.beforeEach((to, from, next) => {
  // to 将要访问的路径
  // from 代表从哪个路径跳转而来
  // next 是一个函数，表示放行
  //     next()  放行    next('/login')  强制跳转
  if (to.matched.length === 0) {
    // 匹配前往的路由不存在
    from.name
      ? next({
        name: from.name
      })
      : next('/errorinfo')
    // 判断此跳转路由的来源路由是否存在，存在的情况跳转到来源路由，否则跳转到404页面
  } else {
    next() // 如果匹配到正确跳转
  }

  if (to.path === '/login') return next()
  // 获取token
  const tokenStr = window.sessionStorage.getItem('userId')
  if (!tokenStr) {
    return next('/login')
  }
  next()
})

export default router
